        opacity: 0;
        transition: all .3s ease 0s;
        visibility: hidden; }
        .active header .overflow {
          z-index: 1;
          opacity: 50%;
          visibility: visible; } }
    @media (max-width: 767px) {
      header .overflow {
        display: none; } }
  @media (max-width: 1199px) {
    header .menu-wrap {
      position: absolute;
