    @media (max-width: 1199px) {
      header .menu-item {
        margin: 0 0 20px 0; } }
  @media (min-width: 1200px) {
    header .home-item {
      display: none; } }
  header .link {
    position: relative;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.7;
    color: #2D2D2D; }
    @media (min-width: 1200px) {
      header .link::before {
        content: '';
        position: absolute;
        display: block;
        height: 1px;
        width: 0%;
        bottom: -3px;
        left: 50%;
        transform: translateX(-50%);
        background-color: #000;
        transition: width .3s ease-in-out; } }
    @media (min-width: 1200px) {
