.btn-border-anim {
  position: relative;
  text-decoration: none;
  background: none;
  border: 0;
  border-radius: 6px;
  box-shadow: inset 0 0 0 2px transparent; }
  .btn-border-anim:after, .btn-border-anim:before {
    content: '';
    display: block;
    position: absolute;
    box-sizing: border-box;
    border: 2px solid transparent;
    border-radius: 6px;
    width: 0;
    height: 0; }
  .btn-border-anim:after {
    top: 0;
    left: 0;
    transition: border-color 0s ease-in 0.8s, width 0.2s ease-in 0.6s, height 0.2s ease-in 0.4s; }
  .btn-border-anim:before {
    bottom: 0;
    right: 0;
    transition: border-color 0s ease-in 0.4s, width 0.2s ease-in 0.2s, height 0.2s ease-in; }
  .btn-border-anim:hover {
    padding: 16px; }
    .btn-border-anim:hover:after, .btn-border-anim:hover:before {
      width: 100%;
      height: 100%;
      border-radius: 6px; }
    .btn-border-anim:hover:after {
      border-top-color: #4379E0;
      border-right-color: #4379E0;
      transition: width 0.2s ease-out, height 0.2s ease-out 0.2s; }
    .btn-border-anim:hover:before {
      border-bottom-color: #4379E0;
      border-left-color: #4379E0;
      transition: border-color 0s ease-out 0.4s, width 0.2s ease-out 0.4s, height 0.2s ease-out 0.6s; }

